import { Component, OnInit } from '@angular/core';
import { from, of, zip, config } from 'rxjs';
import { groupBy, mergeMap, toArray, skip, skipWhile, map, endWith } from 'rxjs/operators';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent implements OnInit {
  title = 'harness';
  currentIndex: number = 0;
  groups: any = new Map();
  arrangementList = [
    { "id": "1", "c360ArrId": 156131 },
    { "id": "2", "parent360ArrId": 156132 },
    { "id": "a", "parent360ArrId": 156132 },
    { "id": "3", "parent360ArrId": 156133 },
    { "id": "a", "parent360ArrId": 156133 },
    { "id": "b", "parent360ArrId": 156133 }
  ];
  changed(event) {
    this.currentIndex = parseInt(event.target.value, 10) + 1;
  }

  add() {
    if(this.currentIndex !== 1) {
      let newdata = { "id": "", "parent360ArrId": this.arrangementList[this.currentIndex].parent360ArrId }
      this.arrangementList.splice(this.currentIndex, 0, newdata);
      this.processArrangementList(newdata.parent360ArrId, true);
    } else {
      let newdata = { "id": this.getLastNumericId(), "parent360ArrId": this.arrangementList[0].c360ArrId }
      this.arrangementList.push(newdata);
    }
  }

  getLastNumericId(): string {
    let arr = this.arrangementList.map(x=>parseInt(x.id)).filter(x => !isNaN(x))
    let lastNumericId = Math.max(...arr) + 1;
    return lastNumericId.toString();
  }

  delete() {
    let deleteIndex = this.currentIndex - 1;
    let changedId = this.arrangementList[deleteIndex].parent360ArrId;
    let changedIndex = this.arrangementList[deleteIndex].id;
    this.arrangementList.splice(deleteIndex, 1);
    this.processArrangementList(changedId, false, changedIndex);
  }

  processArrangementList(changedValue?: any, isAddOperation?: any, changedIndex?: any) {
    from(this.arrangementList).pipe(
      skipWhile(list => list.parent360ArrId === undefined ),
      groupBy(list => list.parent360ArrId),
      mergeMap(group => zip(of(group.key) ,group.pipe(toArray())))
    ).subscribe(array => {
      if(isAddOperation) {
        this.convert(array, changedValue);
      } else {
        console.log(parseInt(changedValue,10), this.arrangementList[0].c360ArrId)
        console.log(changedValue, array[0])
        if(parseInt(changedValue,10) === this.arrangementList[0].c360ArrId && changedValue === array[0]) {
          this.convertNumber(array[1], changedIndex);
          console.log('deleting number')
        } else {
          this.convert(array, changedValue);
          console.log('deleting alphabet')
        }
      }
    });
  }
  
  convertNumber(array, changedIndex) {
    
    array.forEach((element) => {
      if(element.id < parseInt(changedIndex,10)) return;
      element.id = changedIndex.toString();
      changedIndex = parseInt(changedIndex, 10);
      changedIndex++;
    });
  }

  convert(array, changedValue) {
    this.groups.clear();
    this.groups.set(array[0], array[1])
    if(this.groups.get(changedValue)) {
      let counter = 'a'.charCodeAt(0);
      let array = this.groups.get(changedValue);
      array.forEach((element, index) => {
        if(index === 0) return;
        element.id = String.fromCharCode(counter++);
      });
    }
  }
  
  ngOnInit() {}
}
